</template>
<script type="text/ecmascript-6">
import LookImg from '../../../base/lookImg/lookImg'
export default {
  name: 'buyerAccountDetail',
  components: {
    LookImg
  },
  data () {
    return {
      addUserMoney: '',
      deleUserMoney: '',
      addMoney: false,
      deleMoney: false,
      currentPage: 1,
      pageSize: 5,
      pageNo: 1,
      totalCount: 0,
      storageUserInfo: JSON.parse(sessionStorage.getItem('clickUserInfo')),
      canGetOrder: true,
      activeName: 'first',
      addInivite: false,
      editPLUS: false,
      editTb: false,
      turnType: '',
      // 
      showScore: false,
      deleScore: 0,
      addClass: '',
      addInivitePhone: '',
      isPlus: '0',
      plusType: '0',
      plusTime: '',
      fixJdName: '',
      baitiaoStatus: '',
      JDcanGetOrder: '',
      // 
      editTbObj: {
        userName: '',
        gender: '',
        province: '',
        ageArea: '',
        taoNum: '',
        plusType: '',
        accountLeave: '',
        huabei: '',
        canGetOrder: ''
      },
      // 
      provinceArr: [],
      // 
      returnAccount: false,
      returnReason: '',
      // 
      showTop: true,
      // 
      topInfoObj: {},
      // 
      topWechat: {},
      // 
      userInfoObj: {},
      // 
      userMoneyObj: {},
      // 
      withdrawMoneyNum: 0,
      showWithdraw: false,
      // 
      nextBuyerList: [],
      pickerOptions: { // 
        disabledDate (time) {
          return time.getTime() < Date.now()
        }
      },
      // 
      showLookImg: false,
      lookImgUrl: ''
    }
  },
  computed: {
    leastScore: function () {
      let allScore = this.userInfoObj.userScore
      // allScore = allScore - this.deleScore
      // if (allScore > 12) {
      //   this.$message({
      //     type: 'warning',
      //     message: '!'
      //   })
      //   this.deleScore = 0
      //   allScore = this.userInfoObj.userScore
      // }
      return allScore
    },
    leastMoney: function () {
      let allMoney = this.userMoneyObj.availableCapitalAmount
      allMoney = allMoney - this.withdrawMoneyNum
      return allMoney
    }
  },
  methods: {
    lookImg (url) {
      this.lookImgUrl = url
      this.showLookImg = true
    },
    rej (type) {
      if (type === 1) {
        this.returnAccount = true
        this.turnType = 'jingdon'
      } else {
        this.returnAccount = true
        this.turnType = 'taobao'
      }
    },
    sureToTurnBack () {
      console.log(this.turnType)
      if (this.turnType === 'taobao') {
        this.$ajax.post('/api/buyerAccount/rejectTB', {
          buyerAccountId: this.userInfoObj.buyerUserAccountId,
          comment: this.returnReason
        }).then((data) => {
          if (data.data.code === '200') {
            this.$message({
              message: '!',
              type: 'success'
            })
            this.getUserInfo()
          } else {
            this.$message({
              message: data.data.message,
              type: 'warning'
            })
          }
        }).catch((err) => {
          this.$message.error(err)
        })
      } else {
        this.$ajax.post('/api/buyerAccount/rejectJD', {
          buyerAccountId: this.userInfoObj.buyerUserAccountId,
          comment: this.returnReason
        }).then((data) => {
          if (data.data.code === '200') {
            this.$message({
              message: '!',
              type: 'success'
            })
            this.getUserInfo()
            this.returnAccount = false
          } else {
            this.$message({
              message: data.data.message,
              type: 'warning'
            })
          }
        }).catch((err) => {
          this.$message.error(err)
        })
      }
      this.returnAccount = false
    },
    toFixTbInfo () {
      this.$ajax.post('/api/buyerAccount/fixTaobaoInfo', {
        buyerUserAccountId: this.userInfoObj.buyerUserAccountId,
        taobaoName: this.editTbObj.userName,
        isAccept: this.editTbObj.canGetOrder,
        gender: this.editTbObj.gender,
        locationProvince: this.editTbObj.province,
        ageIndex: this.editTbObj.ageArea,
        taoqiValue: this.editTbObj.taoNum,
        vipType: this.editTbObj.plusType,
        accountLevel: this.editTbObj.accountLeave,
        isHuabei: this.editTbObj.huabei
      }).then((data) => {
        if (data.data.code === '200') {
          this.$message({
            message: '!',
            type: 'success'
          })
          this.editTb = false
          this.getUserInfo()
        } else {
          this.$message({
            message: data.data.message,
            type: 'warning'
          })
        }
      }).catch((err) => {
        this.$message.error(err)
      })
    },
    handleClick (tab, event) {
      if (tab.name === 'second') {
        if (this.userInfoObj.parentUserId && this.userInfoObj.parentUserType) {
          let parentUserId = this.userInfoObj.parentUserId
          let parentUserType = this.userInfoObj.parentUserType
          this.getTopInfo(parentUserId, parentUserType)
        } else {
          this.showTop = false
        }
        // 
        this.getRelativeList()
      }
    },
    handleSizeChange (val) {
      this.pageSize = val
      this.getRelativeList()
    },
    handleCurrentChange (val) {
      this.pageNo = val
      this.getRelativeList()
    },
    // plus
    editPlusPost () {
      if (parseInt(this.isPlus) === 0) {
        this.plusType = '2'
        this.plusTime = ''
      }
      this.$ajax.post('/api/buyerAccount/fixJDPlus', {
        buyerUserAccountId: this.userInfoObj.buyerUserAccountId,
        isJDPlus: this.isPlus,
        JDPlusType: this.plusType,
        JDPlusEndDate: this.plusTime,
        jdNickName: this.fixJdName,
        isBlankNote: this.baitiaoStatus,
        isAccept: this.JDcanGetOrder
      }).then((data) => {
        if (data.data.code === '200') {
          this.$message({
            message: '!',
            type: 'success'
          })
          this.editPLUS = false
          this.getUserInfo()
        } else {
          this.$message({
            message: data.data.message,
            type: 'warning'
          })
        }
      }).catch((err) => {
        this.$message.error(err)
      })
    },
    // 
    deleScorePost () {
      this.$ajax.post('/api/buyerAccount/deductScore', {
        buyerUserId: this.userInfoObj.buyerUserAccountId,
        score: this.deleScore
      }).then((data) => {
        if (data.data.code === '200') {
          this.$message({
            message: '!',
            type: 'success'
          })
          this.showScore = false
          this.getUserInfo()
        } else {
          this.$message({
            message: data.data.message,
            type: 'warning'
          })
        }
      }).catch((err) => {
        this.$message.error(err)
      })
    },
    // 
    getUserInfo (isLook) {
      let id = ''
      if (isLook) {
        id = this.$route.query.id
      } else {
        id = this.storageUserInfo.buyerUserAccountId
      }
      this.$ajax.post('/api/buyerAccount/getUserInfoByUserId', {
        buyerUserAccountId: id
      }).then((data) => {
        console.log(data)
        if (data.data.code === '200') {
          let res = data.data.data
          this.userInfoObj = res
        } else {
          this.$message({
            message: data.data.message,
            type: 'warning'
          })
        }
      }).catch((err) => {
        this.$message.error(err)
      })
    },
    // 
    showFixTbAlert () {
      this.editTbObj.userName = this.userInfoObj.taobaoWangNickName || ''
      this.editTbObj.gender = this.userInfoObj.gender || ''
      this.editTbObj.province = this.userInfoObj.locationProvince || ''
      this.editTbObj.ageArea = this.userInfoObj.ageIndex || ''
      this.editTbObj.taoNum = this.userInfoObj.taoqiValue || ''
      this.editTbObj.plusType = this.userInfoObj.tbVipType || ''
      this.editTbObj.accountLeave = this.userInfoObj.taobaoAccountGrade || ''
      this.editTbObj.huabei = this.userInfoObj.isTbHuabei || ''
      this.editTbObj.canGetOrder = this.userInfoObj.isTbAccept || ''
      this.editTb = true
    },
    // 
    getTopInfo (id, type) {
      let api = ''
      let data = {}
      if (parseInt(type) === 2) {
        api = '/api/sellerManagerAccount/getParentSellerManagerByManagerId'
        data = {
          sellerManagerId: id
        }
      } else {
        api = '/api/buyerAccount/getUserInfoByUserId'
        data = {
          buyerUserAccountId: id
        }
      }
      this.getTopChat()
      this.$ajax.post(api, data).then((data) => {
        if (data.data.code === '200') {
          this.topInfoObj = data.data.data
        } else {
          this.$message({
            message: data.data.message,
            type: 'warning'
          })
        }
      }).catch((err) => {
        this.$message.error(err)
      })
    },
    getTopChat () {
      this.$ajax.post('/api/sellerManagerAccount/getParentByBuyerAccountId', {
        buyerAccountId: this.storageUserInfo.buyerUserAccountId
      }).then((data) => {
        if (data.data.code === '200') {
          this.topWechat = data.data.data
        } else {
          this.$message({
            message: data.data.message,
            type: 'warning'
          })
        }
      }).catch((err) => {
        this.$message.error(err)
      })
    },
    // 
    getRelativeList () {
      this.$ajax.post('/api/buyerAccount/getBuyerListByParentUserId', {
        parentUserId: this.storageUserInfo.buyerUserAccountId,
        pageSize: this.pageSize,
        pageNo: this.pageNo
      }).then((data) => {
        if (data.data.code === '200') {
          this.nextBuyerList = data.data.data.buyerUsers
          this.totalCount = data.data.data.totalCount
        } else {
          this.$message({
            message: data.data.message,
            type: 'warning'
          })
        }
      }).catch((err) => {
        this.$message.error(err)
      })
    },
    // 
    confirmAlert (index) {
      if (index === 1) { // 
        this.$confirm('', '?', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$ajax.post('/api/buyerAccount/passConfirm', {
            buyerUserAccountId: this.userInfoObj.buyerUserAccountId
          }).then((data) => {
            if (data.data.code === '200') {
              this.$message({
                type: 'success',
                message: '!'
              })
              this.getUserInfo()
            } else {
              this.$message({
                message: data.data.message,
                type: 'warning'
              })
            }
          }).catch((err) => {
            this.$message.error(err)
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      } else if (index === 2) { // 
        this.$confirm('', '?', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$ajax.post('/api/buyerAccount/confirmAddWechat', {
            buyerUserAccountId: this.userInfoObj.buyerUserAccountId
          }).then((data) => {
            if (data.data.code === '200') {
              this.$message({
                type: 'success',
                message: '!'
              })
              this.getUserInfo()
            } else {
              this.$message({
                message: data.data.message,
                type: 'warning'
              })
            }
          }).catch((err) => {
            this.$message.error(err)
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      } else if (index === 3) { // 
        this.$confirm('', '?', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$ajax.post('/api/buyerAccount/changeInviteable', {
            buyerUserAccountId: this.userInfoObj.buyerUserAccountId
          }).then((data) => {
            if (data.data.code === '200') {
              this.$message({
                type: 'success',
                message: '!'
              })
              this.getUserInfo()
            } else {
              this.$message({
                message: data.data.message,
                type: 'warning'
              })
            }
          }).catch((err) => {
            this.$message.error(err)
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      } else if (index === 4) {
        this.$confirm('', '?', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$ajax.post('/api/buyerAccount/passConfirmTaobao', {
            buyerUserAccountId: this.userInfoObj.buyerUserAccountId
          }).then((data) => {
            if (data.data.code === '200') {
              this.$message({
                type: 'success',
                message: '!'
              })
              this.getUserInfo()
            } else {
              this.$message({
                message: data.data.message,
                type: 'warning'
              })
            }
          }).catch((err) => {
            this.$message.error(err)
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      }
    },
    // 
    toDetail (id) {
      this.$router.push({ name: 'buyerAccountDetail', query: { id: id } })
      this.getUserInfo(this.$route.query.id)
    },
    // 
    addInivitePost () {
      if (this.addClass === '') {
        this.$message({
          type: 'warning',
          message: '!'
        })
      } else if (this.parentTelephone === '') {
        this.$message({
          type: 'warning',
          message: '!'
        })
      } else {
        this.$ajax.post('/api/buyerAccount/fillInviterInfo', {
          buyerUserAccountId: this.userInfoObj.buyerUserAccountId,
          parentType: this.addClass,
          parentTelephone: this.parentTelephone
        }).then((data) => {
          if (data.data.code === '200') {
            this.$message({
              type: 'success',
              message: '!'
            })
            this.getTopInfo(this.userInfoObj.parentUserId, this.userInfoObj.parentUserType)
            this.addInivite = false
          } else {
            this.$message({
              message: data.data.message,
              type: 'warning'
            })
          }
        }).catch((err) => {
          this.$message.error(err)
        })
      }
    },
    // 
    getBuyerMoney () {
      this.$ajax.post('/api/userFund/getBuyerUserFund', {
        buyerUserAccountId: this.storageUserInfo.buyerUserAccountId
      }).then((data) => {
        if (data.data.code === '200') {
          this.userMoneyObj = data.data.data
        } else {
          this.$message({
            message: data.data.message,
            type: 'warning'
          })
        }
      }).catch((err) => {
        this.$message.error(err)
      })
    },
    // 
    getProvince () {
      this.$ajax.post('/api/config/location/getProvinceList', {
      }).then((data) => {
        if (data.data.code === '200') {
          this.provinceArr = data.data.data
        } else {
          this.$message({
            message: '!',
            type: 'warning'
          })
        }
      }).catch((err) => {
        this.$message.error(err)
      })
    },
    // 
    changeMoney (type) {
      if (type === 0) { // 
        this.$ajax.post('/api/userFund/buyer/platformReduceMoney', {
          buyerUserAccountId: this.userInfoObj.buyerUserAccountId,
          amount: this.deleUserMoney
        }).then((data) => {
          if (data.data.code === '200') {
            this.$message({
              message: '!',
              type: 'success'
            })
            this.getBuyerMoney()
            this.deleMoney = false
          } else {
            this.$message({
              message: data.data.message,
              type: 'warning'
            })
          }
        }).catch((err) => {
          this.$message.error(err)
        })
      } else { // 
        this.$ajax.post('/api/userFund/buyer/platformAddMoney', {
          buyerUserAccountId: this.userInfoObj.buyerUserAccountId,
          amount: this.addUserMoney
        }).then((data) => {
          if (data.data.code === '200') {
            this.$message({
              message: '!',
              type: 'success'
            })
            this.getBuyerMoney()
            this.addMoney = false
          } else {
            this.$message({
              message: data.data.message,
              type: 'warning'
            })
          }
        }).catch((err) => {
          this.$message.error(err)
        })
      }
    },
    // 
    withdrawPost () {
      this.$ajax.post('/api/withdrawApply/createBuyerApply', {
        userId: this.userInfoObj.buyerUserAccountId,
        withdrawAmount: this.withdrawMoneyNum,
        withdrawType: '1'
      }).then((data) => {
        if (data.data.code === '200') {
          this.$message({
            message: '!',
            type: 'success'
          })
          this.getBuyerMoney()
          this.showWithdraw = false
        } else {
          this.$message({
            message: data.data.message,
            type: 'warning'
          })
        }
      }).catch((err) => {
        this.$message.error(err)
      })
    }
  },
  mounted () {
    this.getUserInfo()
    this.getBuyerMoney()
    this.getProvince()
  }
}
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
.buyerAccountDetail
  padding 0 20px 20px
  .addBtn
    display inline-block
    margin-left 5px
    width 20px
    height 20px
    line-height 20px
    text-align center
    border 1px solid #979797
    font-size 16px
    cursor pointer
  .btn
    display inline-block
    text-align center
    width 90px
    height 36px
    line-height 36px
    border-radius 4px
    background #40B6FF
    color #ffffff
    cursor pointer
    margin-left 14px
    margin-right 14px
  .longBtn
    width 200px
  .whiteBtn
    background #ffffff
    color #333333
    border 1px solid #cccccc
  .red
    color #FC1733
  .link
    color #1D6AE7
    cursor pointer
  .nav
    height 36px
    span
      font-size 12px
      line-height 36px
      color #262626
  .card
    display flex
    justify-content space-between
    >div
      height 250px
      box-shadow 0 1px 5px rgba(0, 0, 0, 0.2)
      background #ffffff
      border-radius 4px
      flex 1
    .title
      font-size 12px
      line-height 38px
      padding-left 20px
      padding-right 20px
      color #ffffff
    .baseInfo
      margin-right 20px
      h2
        background #FFAD33
      ul
        padding 30px
        li
          text-align center
          font-size 14px
          color #333333
          line-height 30px
          img
            width 60px
            height 60px
            border-radius 50%
          b
            display inline-block
            width 20px
            height 20px
            vertical-align middle
          span
            margin-left 10px
            margin-right 10px
    .coin
      margin-right 20px
      h2
        background #40B6FF
      ul
        display flex
        justify-content center
        margin 20px
        padding-bottom 14px
        padding-top 14px
        border-bottom 1px solid #e5e5e5
        li
          flex 1
          padding 20px 0
          text-align center
          &:first-child
            border-right 1px solid #e5e5e5
          strong
            font-size 28px
            color #333333
            line-height 36px
          p
            font-size 14px
            color #999999
            line-height 26px
      >p
        color #666666
        // line-height 28px
        font-size 14px
        text-align center
    .manger
      h2
        background #FF3341
      ul
        padding-top 40px
        display flex
        justify-content center
        li
          flex 1
          text-align center
          padding 26px 0
          &:first-child
            border-right 1px solid #e5e5e5
          p
            font-size 14px
            line-height 28px
            color #333333
          strong
            font-size 28px
  .tab
    margin-top 16px
    margin-bottom 20px
    padding 20px
    background #ffffff
    .tabCont
      padding 20px
      h2
        font-size 16px
        color #333333
        margin-bottom 20px
      h3
        font-size 14px
        color #333333
        line-height 30px
        padding-left 20px
        padding-right 20px
        span
          float right
      ul
        padding 20px
        margin-bottom 20px
        display flex
        flex-wrap wrap
        border-top 1px solid #e5e5e5
        border-bottom 1px solid #e5e5e5
        li
          flex 1
          border-right 1px solid #e5e5e5
          &:nth-child(2)
            padding-left 50px
          &:last-child
            border none
            text-align center
            align-self center
          img
            width 60px
            vertical-align top
          p
            font-size 14px
            color #666666
            line-height 30px
            b
              color #333333
              font-weight 500
        .jdAccount
          display flex
          text-align center
          p
            flex 1
            a
              display block
      .button
        text-align center
        border-top 1px solid #e5e5e5
        // border-bottom 1px solid #e5e5e5
        margin-bottom 20px
        padding 20px 0
      .fourLi
        padding none
        margin none
        border-bottom none
    .inviteCont
      >div
        h2
          margin-top 20px
          margin-bottom 10px
          font-size 14px
          color #999999
          line-height 26px
          padding-left 20px
          padding-right 20px
        ul
          height 40px
          line-height 40px
          font-size 14px
          color #2C2C2C
          display flex
          justify-content space-between
          background #f8f8f8
          border 1px solid #e5e5e5
          padding-left 20px
          padding-right 20px
          li
            flex 1
      .user
        b
          padding-left 20px
          padding-right 40px
          font-size 14px
          color #2C2C2C
      .next
        table
          width 100%
          font-size 14px
          tr
            border 1px solid #e5e5e5
            color #666666
            height 40px
            line-height 40px
            text-align center
            th
              background #f8f8f8
              color #333333
</style>  


